import View from "./view";

class SortingView extends View {
  constructor() {
    super();
    this.render(this._generateMarkup);
  }
  _parentElement = document.querySelector('.sorting');  
  _generateMarkup() {
    return 
    `
      <form class="sorting">
        <p>sort</p>
        <div class="sorting__inputs">
          <div>
            <input type="radio" name="sort" id="none" value="none" checked/>
            <label for="none">none</label>
          </div>
          <div>
            <input type="radio" name="sort" id="name" value="name" />
            <label for="name">by name</label>
          </div>              
          <div>
            <input type="radio" name="sort" id="time" value="time" />
            <label for="time">by cooking time</label>
          </div>
        </div>
      </form>
    `
  }
}
export default new SortingView();